import { useCallback, useMemo, useTransition } from "react";
import { gql } from "../__generated__";
import { useQuery } from "@apollo/client";
import { ICalendar } from "datebook";
import * as FileSaver from "file-saver";
import { getUserLocale } from "get-user-locale";
import {
  GetEventTimetableDocument,
  GetEventTimetableQuery,
} from "../__generated__/graphql";
import { useTranslation } from "react-i18next";

const ALL_STUFF = gql(`query getEventTimetable(
    $orderBy: eventPhaseOrderByEnum
    $orderDirection: OrderDirectionEnum
    $eventId: Int
  ) {
    getEventTimetable(
      orderBy: $orderBy
      orderDirection: $orderDirection
      eventId: $eventId
    ) {
      id
      phaseName
      eventID
      phaseDateAndTime
      sexCode
      unitName
      unitTypeName
      discipline {
        _id
        name
        typeName
        typeOrder
        order
        nameUrlSlug
        __typename
      }
      isStartlistPublished
      isResultPublished
      isPhaseSummaryPublished
      combinedDisciplineOrder
      phaseOrder
      phaseSessionName
      phaseSessionOrder
      status
      isPointsPublished
      phaseNameUrlSlug
      sexNameUrlSlug
      __typename
    }
  }
`);

const getGender = (gender: string) => {
  switch (gender) {
    case "M":
      return "gender.M";
    case "W":
      return "gender.W";
    case "X":
      return "gender.X";
    default:
      return gender;
  }
};

const getFormattedDate = (dateString: string): string => {
  const lang = getUserLocale();
  console.log("LOCALE!", lang);
  return new Intl.DateTimeFormat(lang, {
    hour: "2-digit",
    minute: "2-digit",
  }).format(new Date(dateString));
};

const getDayLocale = (dateString: string): string => {
  const lang = getUserLocale();
  return new Intl.DateTimeFormat(lang, {
    weekday: "long",
    month: "long",
    day: "2-digit",
  }).format(new Date(dateString));
};

const downloadCalendar = (icl: ICalendar, name = "vm2023.ics"): void => {
  if (!icl) {
    return;
  }
  const blob = new Blob([icl.render()], {
    type: "text/calendar",
  });

  FileSaver.saveAs(blob, name);
};

export const GetCompetitions = () => {
  const { t } = useTranslation();
  const { loading, data } = useQuery(ALL_STUFF, {
    variables: { eventId: 7138987 },
  });

  const eventsByDay = useMemo(
    () =>
      data?.getEventTimetable?.reduce((prev, curr) => {
        const localeDay = new Date(curr.phaseDateAndTime).getDate();
        prev[localeDay] = (localeDay in prev ? prev[localeDay] : []).concat(
          curr
        );
        return prev;
      }, new Map<number, Array<GetEventTimetableQuery["getEventTimetable"][0]>>()),
    [data?.getEventTimetable]
  );

  console.log("events by day", eventsByDay);

  if (loading) {
    return <p>{t("loading")}</p>;
  }

  if (!data) {
    return <p>Error...</p>;
  }

  const getTitleFromEvent = (
    event: GetEventTimetableQuery["getEventTimetable"][0]
  ) => {
    return (
      `${t(event?.discipline?.name)} ${t(getGender(event.sexCode))}` +
      (event?.phaseName !== "Undefined"
        ? `, ${t(event?.phaseName).toLowerCase()}`
        : "")
    );
  };

  const createCalendarEventFrom = (
    curr: GetEventTimetableQuery["getEventTimetable"][0]
  ) => {
    return new ICalendar({
      title: getTitleFromEvent(curr),
      location: "Budapest",
      description: `${curr?.phaseSessionName} - ${curr?.discipline?.typeName}`,
      start: new Date(curr?.phaseDateAndTime),
      end: new Date(
        new Date(curr?.phaseDateAndTime).getTime() + 60 * 30 * 1000
      ),
    });
  };

  const downloadAll = () => {
    const cal = data.getEventTimetable?.reduce(
      (prev: undefined | ICalendar, curr) => {
        const event = createCalendarEventFrom(curr);
        return prev?.addEvent(event) ?? event;
      },
      undefined
    );

    downloadCalendar(cal);
  };

  //   const sexCodes = new Set(
  //     data.getEventTimetable?.map((event) => event?.sexCode)
  //   );
  //   const phaseName = new Set(
  //     data.getEventTimetable?.map((event) => event?.phaseName)
  //   );
  //   const disciplineTypeName = new Set(
  //     data.getEventTimetable?.map((event) => event?.discipline?.typeName)
  //   );
  //   console.log("SEX CODES", sexCodes);
  //   console.log("PHASE", phaseName);
  //   console.log("diciplineTypeName", disciplineTypeName);

  return (
    <>
      <h1>VM2023</h1>
      <button onClick={downloadAll}>{t("downloadAll")}</button>
      {Object.keys(eventsByDay).map((key) => {
        const events = eventsByDay[key] as Array<
          GetEventTimetableQuery["getEventTimetable"][0]
        >;

        return (
          <div>
            <h3>{getDayLocale(events[0].phaseDateAndTime)}</h3>
            {events.map((event) => {
              const title = getTitleFromEvent(event);
              return (
                <li key={event.id}>
                    <span style={{ fontFamily: "monospace" }}>
                      {getFormattedDate(event.phaseDateAndTime)} -{" "}
                    </span>
                    <a
                      onClick={(e) => {
                        e.preventDefault();
                        const ical = createCalendarEventFrom(event);
                        downloadCalendar(
                          ical,
                          `${title
                            .replaceAll(" ", "")
                            .replace(",", "")
                            .toLowerCase()}.ics`
                        );
                      }}
                      href="/"
                    >
                      {title}
                    </a>
                </li>
              );
            })}
          </div>
        );
      })}
    </>
  );
};
